@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap');
body{
  margin: 0;
  padding: 0;
}
* {
  box-sizing: border-box;
}
a{
  text-decoration: none;
  display: block;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
input {
  border: none;
  outline: none;
}
button {
  border: none;
  outline: none;
}
.center {
  width: 100%;
  max-width: 1110px;
  margin: 0 auto;
}
.first_screen {
  background: #F9F6F1;
  height: 100vh;
  padding-top: 50px;
  overflow: hidden;
}
.header_top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.top_menu {
  ul {
    display: flex;
    li {
      a {
        font-family: Montserrat;
font-style: normal;
font-weight: 500;
font-size: 14px;
letter-spacing: 0.03em;
color: #212121;
position: relative;
margin-right: 45px;
transition: all 0.3s ease;
&:after {
  content: "";
  position: absolute;
width: 5px;
height: 4px;
background-image: url("../img/Polygon.png");
top: 5px;
right: -8px;
}
&:hover {
  color: #D7B399;
}
      }
    }
  }
}
.top_menu ul li:last-child a{
  margin-right: 0px;
}
.pay_menu {
  ul {
    display: flex;
    li {
      a{
        font-family: Montserrat;
font-style: normal;
font-weight: 500;
font-size: 14px;
letter-spacing: 0.03em;
color: #212121;
transition: all 0.3s ease;
&:hover {
  color: #D7B399;
}
      }
    }
  }
}
.pay_menu ul li:last-child a {
  margin-left: 35px;
}
.basket {
  display: flex;
  align-items: center;
}
.first_screen_item {
  display: flex;
  &:focus {
    outline: none;
  }
}
.first_screen_img {
  margin-top: -34px;
  position: relative;
  .sale_price {
    position: absolute;
    top: 0;
    right: 0;
  }
}
.title_item {
  font-family: Montserrat;
font-style: normal;
font-weight: normal;
font-size: 30px;
line-height: 37px;
letter-spacing: 0.02em;
margin-bottom: 35px;
color: #000000;
}
.sales {
font-family: Montserrat;
font-style: normal;
font-weight: bold;
font-size: 55px;
line-height: 67px;
letter-spacing: 0.04em;
margin-bottom: 55px;
color: #000000;

}
.look_btn {
  width: 240px;
height: 55px;
  background: #D7B399;
  font-family: Montserrat;
font-style: normal;
font-weight: normal;
font-size: 16px;
letter-spacing: 0.03em;
color: #FFFFFF;
display: flex;
align-items: center;
justify-content: center;
transition: all 0.5s ease;
&:hover{
  background: #F9F6F1;
  color: #D7B399;
}
}
.first_screen_item {
  margin-top: 120px;
}
.delivery_block {
  min-height: 235px;
  background-color: #212121;
  padding-top: 80px;
}
.delivery_block_title {
  font-family: Montserrat;
font-style: normal;
font-weight: bold;
font-size: 30px;
text-align: center;
letter-spacing: 0.02em;
color: #FFFFFF
}
.delivery_block_desc {
  font-family: Montserrat;
font-style: normal;
font-weight: 300;
font-size: 21px;
text-align: center;
color: #FFFFFF;
margin-top: 23px;
}
.old_price {
  font-family: Montserrat;
font-style: normal;
font-weight: bold;
font-size: 24px;
text-align: right;
letter-spacing: 0.05em;
text-decoration-line: line-through;
text-transform: uppercase;
color: #000000;
line-height: 29px;
}
.new_price {
  font-family: Montserrat;
font-style: normal;
font-weight: bold;
font-size: 42px;
line-height: 51px;
text-align: right;
letter-spacing: 0.02em;
text-transform: uppercase;
color: #D84033;
}
.basket_count {
  margin-left: 15px;
}
.male_block {
  display: flex;
}
.man_collection {
  width: 50%;
  background: #F9F6F1;
  min-height: 250px;
  background-image: url("../img/man_collection.png");
  background-repeat: no-repeat;
  background-position-x: right;
  background-position-y: bottom;
  padding-top: 60px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding-right: 221px;
}
.woman_collection {
  width: 50%;
  background: #F4F4F4;
  min-height: 250px;
  background-image: url("../img/woman_collection.png");
  background-repeat: no-repeat;
  background-position-x: right;
  background-position-y: bottom;
  padding-top: 60px;
  padding-left: 36px;
}
.arrow_btn {
  background: #D7B399;
  width: 80px;
height: 30px;
display: flex;
justify-content: center;
align-items: center;
margin-top: 40px;
}
.man_collection_title {
  font-family: Montserrat;
font-style: normal;
font-weight: 500;
font-size: 24px;
line-height: 29px;
letter-spacing: 0.03em;
color: #000000;
}
.woman_collection_title {
  font-family: Montserrat;
font-style: normal;
font-weight: 500;
font-size: 24px;
line-height: 29px;
letter-spacing: 0.03em;

color: #000000;
}
.man_collection_desc {
  font-family: Montserrat;
font-style: normal;
font-weight: 300;
font-size: 16px;
line-height: 20px;
margin-top: 25px;
color: #000000;
}
.woman_collection_desc {
  font-family: Montserrat;
font-style: normal;
font-weight: 300;
font-size: 16px;
line-height: 20px;
margin-top: 25px;
color: #000000;
}
.slide_dot {
  display: flex;
  justify-content: center;
  margin-bottom: 50px;
  margin-top: 40px;
  li button {
    font-size: 0;
    width: 40px;
height: 7px;
background: #CECECE;
cursor: pointer;
margin-right: 15px;
  }
}
.slide_dot .slick-active button {
  background: #D7B399;
}
.product_block {
  margin-top: 150px;
}
.product_block_title {
  font-family: Montserrat;
font-style: normal;
font-weight: bold;
font-size: 36px;
letter-spacing: 0.04em;
text-transform: uppercase;
color: #212121;
text-align: center;
line-height: 44px;
}
.product_item_wrap {
  margin-top: 100px;
  display: flex;
  justify-content: space-between;
}
.product_item {
  width: 255px;
height: 350px;
padding-top: 22px;
display: flex;
flex-direction: column;
align-items: center;
box-shadow:
0px -8px 0px -4px #fff,
0px 8px 0px -4px #fff,
8px 0px 0px -4px #fff,
-8px 0px 0px -4px #fff,
0px 0px 0px 3px #D7B399;
}
.product_item_name {
  font-family: Montserrat;
font-style: normal;
font-weight: 600;
font-size: 14px;
line-height: 17px;
text-align: center;
color: #000000;
width: 112px;
margin-top: 25px;
}
.product_item_price {
  font-family: Montserrat;
font-style: normal;
font-weight: bold;
font-size: 18px;
line-height: 22px;
text-align: center;
color: #D7B399;
margin-top: 15px;
}
.product_btn {
  width: 220px;
height: 55px;
display: flex;
padding-left: 35px;
align-items: center;
font-family: Montserrat;
font-style: normal;
font-weight: normal;
font-size: 16px;
letter-spacing: 0.03em;
color: #FFFFFF;
background: #D7B399;
margin: 0 auto;
margin-top: 100px;
position: relative;
&::after {
  content: "";
  width: 20px;
  height: 8px;
  background-image: url(../img/arrow_btn.png);
  position: absolute;
  top: 22px;
  right: 33px;
}
}
